.container{
  display: -ms-flexbox;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.component{
  margin: 1em;
}
.cell{
  display: -ms-flexbox;
  display: flex;
  background-color: lightgray;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 1em;
  padding: 1em;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border: 1px solid grey;
}
.leadForm{
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}
.properties{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.cellTitle{
  padding: 0;
  padding-bottom: .5em;
  margin: 0;
}
.app-root{
  display: -ms-flexbox;
  display: flex;
}
.table{
  border: 1px solid grey;
  display: -ms-flexbox;
  display: flex;
  background-color: lightgray;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 1em;
  margin-right: 1em;
}
.table-title{
  margin-left: 1em;
  margin-bottom: 0;
}


